import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { qAndATool } from './lib';
import reportWebVitals from './reportWebVitals';

// Expose qAndATool globally for direct function calls
window.qAndATool = qAndATool;

// For backward compatibility with existing code
let disabled = false;
let isLoggedIn = true;

if (window.isAnonymous === true) {
  disabled = true;
  isLoggedIn = false;
}

// Popup Chat widget - for backward compatibility
const existingQaBot = document.getElementById('qa-bot');
if (existingQaBot) {
  // If the div exists with id="qa-bot", use it with the new API
  qAndATool({
    target: existingQaBot,
    isLoggedIn: !window.isAnonymous,
    disabled: disabled
  });
} else {
  // Legacy auto-creation behavior
  const domNode = document.createElement('div');
  const root = ReactDOM.createRoot(domNode);

  root.render(
    <React.StrictMode>
      <App disabled={disabled} isLoggedIn={isLoggedIn} />
    </React.StrictMode>
  );
  document.body.appendChild(domNode);
}

// Look for optional elements for an embedded chat widget - for backward compatibility
const embeddedQABots = document.querySelectorAll('.embedded-qa-bot');
embeddedQABots.forEach(embeddedQABot => {
  // welcome message and prompt are data- attributes
  const welcome = embeddedQABot.dataset.welcome;
  const prompt = embeddedQABot.dataset.prompt;

  // Use the new API
  qAndATool({
    target: embeddedQABot,
    embedded: true,
    welcome: welcome,
    prompt: prompt,
    isLoggedIn: isLoggedIn,
    disabled: disabled,
    isOpen: true // Embedded widgets are typically open by default
  });
});

// attach login event to the disabled chatbot input
const loginHandler = () => {
  if (disabled) {
    const loginUrl = '/login?destination=' + window.location.pathname;
    window.location = loginUrl;
  }
}
const addCustomEventListener = (selector, event, handler) => {
  let rootElement = document.querySelector('body');
  rootElement.addEventListener(event, function (evt) {
          var targetElement = evt.target;
          while (targetElement != null) {
              if (targetElement.matches(selector)) {
                  handler(evt);
                  return;
              }
              targetElement = targetElement.parentElement;
          }
      },
      true
  );
}
//adding the Event Listeners to all the chatbot instances
addCustomEventListener('.rcb-chat-input','click',loginHandler);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
